.header{
display: flex;
justify-content: space-between;
align-items: center;
padding: 1rem;
position: fixed;
width: 100%;
height: 90px;
z-index: 10;
}

.header-bg{
    background: rgb(0,0,0,0.85);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 1.5rem;
}

.nav-menu li a{
    font-size: 1.5rem;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
.nav-menu{
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    top: 0;
    left: -100%;
    background: rgba(0, 0, 0, 0.9);
    z-index: -3;
    transition: 0.3s;
}

.nav-menu.active{
    left: 0;
}

.nav-menu li{
    padding: 1rem 0;
}

.nav-menu li a{
    font-size: 2rem;
}

.hamburger{
    display: initial;
}
}

