.about{
    width: 100%;
    margin:3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .container1{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px
}

.about .container1 p{
    margin: 1.2rem 0;
}

.about .container2{
    max-width: 700px;
}

.container2 .image-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr );
    position: relative;
    align-items: center;
    text-align: center;
    
}

.container2 .img{
max-width: 60%;
border: 1px solid #333;
}

.container2 .top{
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.container2 .bottom{
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}



